.game-area {
  position: relative;
    margin: 50px auto;
    width: 30vw;
    height: 50vh;
    background-color: #54d14b;
    border: 4px solid #000;
    border-top: 4px solid #54d14b;
  }

.snake-dot {
  position: absolute;
  width: 2vh;
  height: 2vh;
  background-color: #b1cb47;
  border: 1px solid #69764a;
  z-index: 2;
  border-radius: 50%;
}
.snake-dot:last-child {
  background-image: url(./img/head.png);
  background-repeat: no-repeat;
  background-size: 4vh;
  background-position: center;
}
.snake-food {
  position: absolute;
  width: 2vh;
  height: 2vh;
  /* background-color: #f00; */
  background-image: url(./img/apple.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  /* border: 1px solid #fff; */
  z-index: 1;
}
.counter {
  position: absolute;
  font-family: 'Arial', sans-serif;
  font-weight: 200;
  color: #fff;
  top: -48px;
  background-color: #54d04b;
  left: -4px;
  width: 30vw;
  border: 4px solid #000;
  border-bottom: 4px dashed #000;
  padding-bottom: 15px;
}